    drawPossibleRoad(topHead,bottomHead){
        let [x1,y1] = this.findPositionOfIntersection(topHead)
        let [x2,y2] = this.findPositionOfIntersection(bottomHead)
        const angle1 = degToRad(findAngle2Point(x1,y1,x2,y2))
        const angle2 = degToRad(findAngle2Point(x2,y2,x1,y1))
        const changeDistance=7
        x1+= Math.cos(angle1)*changeDistance
        y1-= Math.sin(angle1)*changeDistance
        x2+= Math.cos(angle2)*changeDistance
        y2-= Math.sin(angle2)*changeDistance

        const shape = this.roadElement.addShape({
            kind:'line',
            lineWidth:9,
            points:[[x1,y1],[x2,y2]],
            color:'white',
            border:true,
            borderWidth:3,
            borderColor:'black'
        })
        return shape
    }

