import View from '../View/view'
import Model from '../Model/model'
import axios from 'axios'
import io from 'socket.io-client'

//actions:settlement,city,road,dice,turn,trade
const Controller = async (canvas,token)=>{
    const socket = io.connect('/',{query:{token}})

    const sendMessage = (message,data)=>{
        console.log(message,data)
        socket.emit(message,data)
    }

    const messageReference = ['Trade with Bank','Trade Response','Trade Refuse','Trade Acceptance','Trade Initiation','Waiting for Choose Road location','Road location chosen','Choose Road location','Waiting for Choose Knight location','Waiting for Discard Cards','Discard Cards','Monopoly resource chosen','Use development card','Choose Monopoly resources','Monopoly resource chosen','Cheat Done','Cheat','Build Settlement Initial','Build Road Initial','Build road','Build settlements','Build city','Your development card','Cheat Done','Error','Start turn timer','Cancel turn timer','Game starting','Action Needed','Move Knight','Resource Stolen','New player','Game data','Dice Result','Plenty card chosen','Change in extra condition','Waiting for Choose Settlement Resources','Waiting for Build Settlement Initial','Waiting for Build Road Initial']
    const usualFunctionReference = messageReference.map(e=>()=>{})
    const waitingFunctionReference = messageReference.map(e=>[])
    const errorFunctionReference = messageReference.map(e=>[])
    const resourceReference = ['wheat','rock','sheep','wood','brick']
    const translateToApplicableResources = (resources)=>{
        const result = {}
        resources.forEach((e,i)=>result[resourceReference[i]] = e)
        return result
    }

    const addMessage = (mess,func,addOnce)=>{
        if(messageReference.indexOf(mess) === -1) return
        usualFunctionReference[messageReference.indexOf(mess)] = func
    }

    const awaitResponse = (message)=>{
        return new Promise((resolve,rejected)=>{
            waitingFunctionReference[messageReference.indexOf(message)].push(resolve)
            errorFunctionReference[messageReference.indexOf(message)].push(rejected)
        })
    }

    const messageReceived = async (message,data)=>{
        console.log(message,data)
        const index = messageReference.indexOf(message)
        await usualFunctionReference[index](data)
        waitingFunctionReference[index].forEach(f=>f(data))
        waitingFunctionReference[index] = []
        errorFunctionReference[index] = []
    }

    const updateLeaderBoard = ()=>{
        model.updateLeaderBoard()
        view.updateLeaderBoard()
    }

    messageReference.forEach(m=>socket.on(m,(data)=>messageReceived(m,data)))

    const global = {
        turnSpecificInfo:{},
        startRobberDrag:()=>{},
        phase:0
    }
    let view = new View(canvas)
    let model = new Model()

    let playerCode = ['blue','red','white','orange','black']
    let announce = (message,code)=>{
        view.onAnnouncementPerpetual = true
        view.changeAnnoucementPerpetual(message,playerCode[code])
    }
    let quickAnnounce = (message,code=global.turn)=>{
        view.addToAnnoucementQueue(message,playerCode[code],1000)
    }
    let turnOffAnnouncement = ()=>{
        view.onAnnouncementPerpetual = false
        view.updateAnnouncement()
    }

    view.configureColorCode(playerCode,global)

    addMessage('Error',({error})=>{
        announce(error)
        console.log(error)
        errorFunctionReference.forEach((e,i)=>{
            if(e.length>0){
                for(let f of e){f(error)}
            }
            errorFunctionReference[i] = []
            waitingFunctionReference[i] = []
        })
    })

    addMessage('Game data',({gameData})=>{
        const data = gameData
        announce(`Room Code: ${data.roomCode}`)
        model.loadData(data)
        view.receiveData(data)
        view.createLeaderboardObject(model,initiateTrade)
        view.createTradingObject(model.usernames,model)
        view.configureStealInterface(model)
        view.createDevDeckPlayer(model,devCardDelegate)
        
        let currentX = view.robberElement.x
        let currentY = view.robberElement.y

        const moveBack = (func)=>{
            let duration = 500
            view.robberElement.addAnimation('x',currentX,duration,func)
            view.robberElement.addAnimation('y',currentY,duration)
        }
        let resolve = ()=>{}
        let ry = 0
        let rx = 0

        view.robberElement.makeDraggable(null,()=>{
            let position;
            let hex;
            BigLoop:for(let y = 0;y<view.hexMap.length;y++){
                for(let x = 0;x<view.hexMap[y].length;x++){
                    if(y+1 === ry && x+1===rx) continue
                    if(view.hexMap[y][x].pointInShape(view.robberElement.x,view.robberElement.y)){
                        position = [y,x]
                        hex = view.hexMap[y][x]
                        resolve([y+1,x+1])
                        break BigLoop
                    }
                }
            }
            if(!position) moveBack()
            else{
                view.robberElement.addAnimation('x',hex.absoluteX,500)
                view.robberElement.addAnimation('y',hex.absoluteY,500)
                view.robberElement.draggable = false
            }
        })
        view.robberElement.draggable = false
        global.startRobberDrag = ()=>{
            currentX = view.robberElement.x
            currentY = view.robberElement.y
            ry = model.grid.robberPosition[0]
            rx = model.grid.robberPosition[1]
            view.robberElement.draggable = true
            return new Promise((r)=>{
                resolve = r
            })
        }
        view.initiateStartGameMessage(()=>sendMessage('Start game'))
    },true)

    addMessage('New player',(data)=>{
        console.log(data)
        model.newPlayer(data)
        updateLeaderBoard()
    },true)

    addMessage('Game starting',async ()=>{
        sendMessage('Cheat')
        model.wheatAmount += 5
        model.rockAmount += 5
        model.sheepAmount += 5
        model.brickAmount += 5
        model.woodAmount += 5
        model.myDevCards ={
            Knight:1,
            Monopoly:1,
            Road:1,
            Point:1,
            Plenty:1
        }
        view.updateToModel()
        announce('The game is starting')
        await awaitResponse('Waiting for Build Settlement Initial')
        announce('Please choose where to put your settlement:')
        const firstSettlementLocation = await view.selectSettlementLocationFrom(model.possibleSettlementsInitial)
        view.buildSettlement(firstSettlementLocation,playerCode[model.playerid])
        model.selfBuildSettlement(firstSettlementLocation)
        sendMessage('Build Settlement Initial',{position:firstSettlementLocation})
        let {resource} = await awaitResponse('Build Settlement Initial')
        model.addResourceInitial(resource)
        await awaitResponse('Waiting for Build Road Initial')
        announce("Please choose where to put your road:")
        const firstRoadLocation = await view.selectRoadLocation(model.possibleRoadsInitial(firstSettlementLocation))
        view.buildRoad(firstRoadLocation[0],firstRoadLocation[1],playerCode[model.playerid])
        model.selfBuildRoad(firstRoadLocation)
        sendMessage('Build Road Initial',{roadPosition:firstRoadLocation,settlementPosition:firstSettlementLocation})
        await awaitResponse('Build Road Initial')
        await awaitResponse('Waiting for Build Settlement Initial')
        announce("Please choose where to put your settlement:")
        const secondSettlementLocation = await view.selectSettlementLocationFrom(model.possibleSettlementsInitial)
        view.buildSettlement(secondSettlementLocation,playerCode[model.playerid])
        model.selfBuildSettlement(secondSettlementLocation)
        sendMessage('Build Settlement Initial',{position:secondSettlementLocation})
        let result = await awaitResponse('Build Settlement Initial')
        resource = result.resource
        model.addResourceInitial(resource)
        await awaitResponse('Waiting for Build Road Initial')
        announce('Please choose where to put your road:')
        const secondRoadLocation = await view.selectRoadLocation(model.possibleRoadsInitial(secondSettlementLocation))
        view.buildRoad(...secondRoadLocation,playerCode[model.playerid])
        model.selfBuildRoad(secondRoadLocation)
        sendMessage('Build Road Initial',{roadPosition:secondRoadLocation,settlementPosition:secondSettlementLocation})
        await awaitResponse('Build Road Initial')
        turnOffAnnouncement()
    },true)

    const changeToPhaseOne = ()=>{
        global.phase = 1
        view.currentActions = ['dice','trade']
        view.actionFunctions = view.currentActions.map(e=>actionFunctions[e])
        view.updateActionObjects()
    }

    const changeToPhaseTwo = ()=>{
        global.phase = 2
        view.currentActions = ['trade',...model.possibleActions,'turn']
        view.actionFunctions = view.currentActions.map(e=>actionFunctions[e])
        view.updateActionObjects()
    }

    const changeToWaitingPhase = ()=>{
        global.phase = 3
        view.currentActions = []
        view.actionFunctions = []
        view.updateActionObjects()
    }

    const actionFunctionFactory = (func)=>async (data)=>{
        changeToWaitingPhase()
        try{
            await func(data)
            view.changePlayerResourceCount(model)
            updateLeaderBoard()
        }catch(e){
            console.log(e)
        }
    }

    const phase1ActionFunctionFactory = (func)=>async (data)=>{
        await actionFunctionFactory(func)(data)
        changeToPhaseOne()
    }
    const phase2ActionFunctionFactory = (func)=>async (data)=>{
        await actionFunctionFactory(func)(data)
        changeToPhaseTwo()
    }

    const handleRobber = async ()=>{
        await awaitResponse('Waiting for Choose Knight location')
        announce('Please choose robber location:')
        const position = await global.startRobberDrag()
        model.changeRobberPostion(...position)
        sendMessage('Choose Knight location',position)
        const robabble = model.findWhoToStealFrom(position)
        await awaitResponse('Move Knight')
        if(robabble.length){
            announce('Who do you want to steal from?')
            const id = await view.initiateStealFromOther(robabble,robabble.map(e=>playerCode[e]))
            sendMessage('Choose who to steal from',id)
            const {resource,robbed} = await awaitResponse('Resource Stolen')
            model[resource] += 1
            model.resourcePlayers[robbed]--
        }
        turnOffAnnouncement()
    }

    addMessage('Discard Cards',async ({resource,id})=>{
        const totalResource = resource.wheat + resource.sheep + resource.rock + resource.brick + resource.wood
        if(id === model.playerid){
            model.minusResource(resource)
        }
        else{
            model.resourcePlayers[id] -= totalResource
        }
    })

    addMessage('Dice Result',async ({dice,resource})=>{
        announce(`${model.usernames[global.turn]} rolled ${dice}`,global.turn)
        await view.rollDiceAnimation(dice)
        if(dice !== 7){
            const myResource = resource[model.playerid]
            myResource.forEach(([resource,amount])=>{
                model[`${resource.toLowerCase()}Amount`]+=amount
            })
            view.changePlayerResourceCount(model)
            turnOffAnnouncement()
        }
        else{
            const totalResources = model.wheatAmount + model.sheepAmount + model.rockAmount + model.brickAmount + model.woodAmount
            if(totalResources > 7){
                announce("You have more than 7 cards!",model.playerid)
                let resource = await view.initiateSteal(Math.ceil(totalResources/2))
                resource = translateToApplicableResources(resource)
                sendMessage('Discard Cards',{resource})
            }
            await handleRobber()
        }
        turnOffAnnouncement()
    })

    const actionFunctions = {
        dice:actionFunctionFactory(async ()=>{
            sendMessage('Roll Dice')
            await awaitResponse('Dice Result')
            changeToPhaseTwo()
        }),
        trade:()=>{initiateTrade()},
        settlement:phase2ActionFunctionFactory(async ()=>{
            announce("Settlement where?")
            const location = await view.selectSettlementLocationFrom(model.possibleSettlements)
            turnOffAnnouncement()
            sendMessage('Build settlements',{position:location})
            await awaitResponse('Build settlements')
            view.buildSettlement(location)
            model.selfBuildSettlement(location)
            model.sheepAmount --
            model.wheatAmount --
            model.brickAmount --
            model.woodAmount --
            view.changePlayerResourceCount(model)
        }),
        city:phase2ActionFunctionFactory(async ()=>{
            announce("Click on a settlement to upgrade:")
            const location = await view.activateCitySelection()
            turnOffAnnouncement()
            sendMessage('Build city',{position:location})
            await awaitResponse('Build city')
            view.buildCity(location)
            model.selfBuildCity(location)
            model.rockAmount -= 3
            model.wheatAmount -= 2
            view.changePlayerResourceCount(model)
        }),
        road:phase2ActionFunctionFactory(async ()=>{
            announce("Road where?")
            const location = await view.selectRoadLocation(model.possibleRoads)
            turnOffAnnouncement()
            sendMessage('Build road',{position:location})
            await awaitResponse('Build road')
            console.log(location)
            view.buildRoad(location[0],location[1])
            model.selfBuildRoad(location)
            model.brickAmount --
            model.woodAmount --
            view.changePlayerResourceCount(model)
        }),
        turn:()=>{
            sendMessage('Pass Turn')
            changeToWaitingPhase()
        }
    }

    addMessage('Action Needed',({turn})=>{
        view.changePlayerResourceCount(model)
        updateLeaderBoard()
        global.turnSpecificInfo = {}
        if(turn === model.playerid){
            changeToPhaseOne()
        }
        else{
            changeToWaitingPhase()
            announce(`${model.usernames[turn]} is playing his turn.`,playerCode[turn])
        }
        global.turn = turn
        view.updateTurn(turn)
    })
    
    const devCardFunctionFactory = (message,func)=>async ()=>{
        console.log('playing dev card',message,func)
        if(global.turnSpecificInfo.playedDevelopmentCard) return
        global.turnSpecificInfo.playedDevelopmentCard = true
        const previousPhase = global.phase
        if(global.phase === 3) return
        changeToWaitingPhase()
        try{
            await func(previousPhase)
            view.changePlayerResourceCount(model)
            updateLeaderBoard()
        }
        catch(e){
            console.log(e)
        }
        console.log('hello')
        console.log(previousPhase)
        if(previousPhase===1) changeToPhaseOne()
        else changeToPhaseTwo()
    }

    const phase2DevCardFunctionFactory = (message,func)=>devCardFunctionFactory(message,async (previousPhase)=>{
        if(previousPhase !== 2) throw Error()
        sendMessage('Use development card',{card:message})
        await awaitResponse('Use development card')
        model.useDevelopmentCard(message)
        view.updateToModel()
        await func()
    })

    const devCardDelegate = {
        Knight:devCardFunctionFactory('Knight',async ()=>{
            sendMessage('Use development card',{card:'Knight'})
            await awaitResponse('Use development card')
            model.useDevelopmentCard('Knight')
            view.updateToModel()
            await handleRobber()
        }),
        Point:()=>{},
        Plenty:phase2DevCardFunctionFactory('Plenty',async()=>{
            let resource = await view.initiatePlenty()
            resource = translateToApplicableResources(resource)
            sendMessage('Choose Plenty resources',resource)
            console.log('hello')
            await awaitResponse('Plenty card chosen')
            console.log('hello1')
            model.addResource(resource)
            console.log(resource)
            view.changePlayerResourceCount(model)
        }),
        Monopoly:phase2DevCardFunctionFactory('Monopoly',async()=>{
            let resource = await view.initiateMonpolyResourceChoosing()
            sendMessage('Choose Monopoly resources',resource)
            let {change} = await awaitResponse('Monopoly resource chosen')
            console.log(change)
            model.changeResourceMonopoly(change,resource,model.playerid)
            view.changePlayerResourceCount(model)
        }),
        Road:phase2DevCardFunctionFactory('Road',async()=>{
            await awaitResponse('Waiting for Choose Road location')
            if(model.selfRoad.length>=13) return
            const firstPosition = await view.selectRoadLocation(model.possibleRoads)
            console.log(firstPosition)
            view.buildRoad(...firstPosition)
            model.buildRoad(firstPosition,model.playerid)
            const secondPosition = await view.selectRoadLocation(model.possibleRoads)
            view.buildRoad(...secondPosition)
            model.buildRoad(secondPosition,model.playerid)
            sendMessage('Choose Road location',{positions:[firstPosition,secondPosition]})
            await awaitResponse('Road location chosen')
        })
    }

    let alreadyBuying = false

    view.devDeck.pressedEvent(async ()=>{
        if(alreadyBuying) return
        if(!model.selfCanBuyDevelopmentCard) return
        if(model.playerid === global.turn && !global.turnSpecificInfo.boughtDevelopmentCard){
            alreadyBuying = true
            global.turnSpecificInfo.boughtDevelopmentCard = true
            sendMessage('Buy development card')
            const {card} = await awaitResponse('Your development card')
            model.buyDevCard(card)
            view.animateDevCardToPlayerDeck()
            view.updateToModel()
            model.wheatAmount--
            model.sheepAmount--
            model.rockAmount--
            view.changePlayerResourceCount(model)
            alreadyBuying = false
        }
    })

    //Passive info
    addMessage('Change in extra condition',({armyChange,roadChange})=>{
        if(armyChange) model.largestArmyPlayer = armyChange
        if(roadChange) model.longestRoadPlayer = roadChange
        updateLeaderBoard()
    })
    
    const passiveFunctionFactory = (func)=>async (...args)=>{
        if(global.turn === model.playerid) return
        await func(...args)
        updateLeaderBoard()
    }

    const attachPassiveFunctionFactory = (message,func)=>addMessage(message,passiveFunctionFactory(func))

    attachPassiveFunctionFactory('Build settlements',({position})=>{
        model.buildSettlement(position,global.turn)
        view.buildSettlement(position,playerCode[global.turn])
        model.resourcePlayers[global.turn]-=4
        quickAnnounce(`${model.usernames[global.turn]} is building a settlement`)
    })
    attachPassiveFunctionFactory('Build city',({position})=>{
        model.buildCity(position,global.turn)
        view.buildCity(position,playerCode[global.turn])
        model.resourcePlayers[global.turn]-=5
        quickAnnounce(`${model.usernames[global.turn]} is building a city`)
    })
    attachPassiveFunctionFactory('Build road',({position})=>{
        model.buildRoad(position,global.turn)
        view.buildRoad(...position,playerCode[global.turn])
        model.resourcePlayers[global.turn]-=2
        quickAnnounce(`${model.usernames[global.turn]} is building a road`)
    })
    attachPassiveFunctionFactory('Use development card',({card})=>{
        quickAnnounce(`${model.usernames[global.turn]} is playing a ${card} development card`)
        model.devCardPlayers[global.turn]--
    })
    attachPassiveFunctionFactory('Buy development card',()=>{
        quickAnnounce(`${model.usernames[global.turn]} is buying a development card`)
        model.devCardPlayers[global.turn]++
    })
    attachPassiveFunctionFactory('Move Knight',({position})=>{
        model.grid.robberPosition = position
        view.moveRobber(position)
    })
    attachPassiveFunctionFactory('Resource Stolen',({resource,robbed})=>{
        //resource is full wheatAmount/rockAmount
        if(robbed === model.playerid) model[resource]++
        else{
            model.resourcePlayers[robbed]--
            model.resourcePlayers[global.turn]++
        }
    })
    attachPassiveFunctionFactory('Plenty card chosen',()=>{
        model.resourcePlayers[global.turn]+=2
    })
    attachPassiveFunctionFactory('Build Settlement Initial',({position,resource,turn})=>{
        model.resourcePlayers[turn]+=resource.length
        model.buildSettlement(position,turn)
        view.buildSettlement(position,playerCode[turn])
        quickAnnounce(`${model.usernames[turn]} built a settlement`,playerCode[turn])
    })
    attachPassiveFunctionFactory('Build Road Initial',({roadPosition,turn})=>{
        model.buildRoad(roadPosition,turn)
        view.buildRoad(...roadPosition,playerCode[turn])
        quickAnnounce(`${model.usernames[turn]} built a road`,playerCode[turn])
    })
    attachPassiveFunctionFactory('Monopoly resource chosen',({resource,change})=>{
        model.changeResourceMonopoly(change,resource,global.turn)
        quickAnnounce(`${model.usernames[global.turn]} steal everyones ${resource.toLowerCase()}!`)
    })
    attachPassiveFunctionFactory('Road location chosen',({positions})=>{
        model.buildRoad(positions[0],global.turn)
        model.buildRoad(positions[1],global.turn)
        view.buildRoad(...positions[0],playerCode[global.turn])
        setTimeout(()=>{
            view.buildRoad(...positions[1],playerCode[global.turn])
        },1000)
    })

    addMessage('End game',({winner})=>{
        announce(`${model.usernames[winner]} had won the game!`)
    })

    const myTradingCodes = []
    const myTradingPartner = []

    const getRidOfCode = (code)=>{
        let index = myTradingCodes.indexOf(code)
        myTradingCodes.splice(index,1)
        myTradingPartner.splice(index,1)
    }
    const initiateTrade = async ()=>{
        try{
            console.log('ehllo')
            let [mode,amountGiven,amountDemand,proposeTo] = await view.initiateTrade()
            console.log(mode,amountGiven,amountDemand,proposeTo)
            amountGiven = translateToApplicableResources(amountGiven)
            amountDemand = translateToApplicableResources(amountDemand)
            if(mode === 'bank'){
                sendMessage('Trade with Bank',{resource:[amountGiven,amountDemand]})
                await awaitResponse('Trade with Bank')
                model.addResource(amountDemand)
                model.minusResource(amountGiven)
            }else{
                sendMessage('Trade Initiation',{resource:[amountGiven,amountDemand],players:proposeTo.map(e=>model.usernames.indexOf(e))})
                const {codes,players} = await awaitResponse('Trade Initiation')
                codes.forEach(c=>myTradingCodes.push(c))
                players.forEach(p=>myTradingPartner.push(p))
            }
        } catch(e){

        }
    }
    const translateToArray = ({wheat,rock,sheep,wood,brick})=>[wheat,rock,sheep,wood,brick]

    const handleTrade = async (user,resourceDemand,resourceGiven,code)=>{
        try{
            let [receiveMode,mode,given,demand] = await view.receiveTrade(user,translateToArray(resourceDemand),translateToArray(resourceGiven))
            if(receiveMode === 'Accept'){
                sendMessage('Trade Acceptance',{code,resources:[resourceGiven,resourceDemand]})
                const changeToPartner = -translateToArray(resourceDemand).reduce((t,e)=>t+e,0) + translateToArray(resourceGiven).reduce((t,e)=>t+e,0)
                model.resourcePlayers[myTradingPartner.indexOf(code)]+=changeToPartner
                model.minusResource(resourceDemand)
                model.addResource(resourceGiven)
                await awaitResponse('Trade Acceptance')
                getRidOfCode(code)
            }
            else{
                sendMessage("Trade Response",{resources:[given,demand],code})
                await awaitResponse('Trade Response')
            }
        }catch(e){
            console.log(e)
            //Refuse Trade
            sendMessage('Trade Refuse')
            getRidOfCode(code)
            await awaitResponse("Trade Refuse")
        }
        view.changePlayerResourceCount()
    }

    addMessage('Trade Response',async ({code,resources})=>{
        if(!myTradingCodes.includes(code)) return
        let [resourceDemand,resourceGiven] = resources
        let user = model.usernames[myTradingPartner.indexOf(code)]
        handleTrade(user,resourceDemand,resourceGiven,code)
    })
    attachPassiveFunctionFactory('Trade Initiation',({players,codes,resources})=>{
        if(!players.includes(model.playerid)) return
        myTradingCodes.push(codes[players.indexOf(model.playerid)])
        myTradingPartner.push(global.turn)
        handleTrade(global.turn,resources[0],resources[1],codes[players.indexOf(model.playerid)])
    })
    addMessage('Trade Refuse',({code})=>{
        if(myTradingCodes.includes(code)) getRidOfCode(code)
    })
    addMessage('Trade Acceptance',({code,resources,players})=>{
        if(!myTradingCodes.includes(code)){
            const [giver,getter] = players
            const [resourceDemand,resourceGiven] = resources
            const changeToPartner = -translateToArray(resourceDemand).reduce((t,e)=>t+e,0) + translateToArray(resourceGiven).reduce((t,e)=>t+e,0)
            model.resourcePlayers[giver] -= changeToPartner
            model.resourcePlayers[getter] += changeToPartner
        }
        else{
            const otherPlayer = myTradingPartner[myTradingCodes.indexOf(code)]
            getRidOfCode(code)
            const [resourceDemand,resourceGiven] = resources
            const changeToPartner = -translateToArray(resourceDemand).reduce((t,e)=>t+e,0) + translateToArray(resourceGiven).reduce((t,e)=>t+e,0)
            model.resourcePlayers[otherPlayer]+=changeToPartner
            model.minusResource(resourceDemand)
            model.addResource(resourceGiven)
            view.changePlayerResourceCount()
        }
    })
    attachPassiveFunctionFactory('Trade with Bank',({resource})=>{
        let [give,take] = resource
        let change = -translateToArray(give).reduce((t,e)=>t+e,0) + translateToArray(take).reduce((t,e)=>t+e,0)
        model.resourcePlayers[global.turn] += change
    })
}

export default Controller