import React, { useRef, useEffect, useState } from 'react';
import "bootstrap/dist/css/bootstrap.min.css"
import {Formik} from 'formik'
import {object,string} from 'yup'
import axios from 'axios'
import cogoToast from 'cogo-toast'
import {BrowserRouter as Router,Link,Route,Switch} from 'react-router-dom'
import Controller from './Controller/controller'

const baseURL = (process.env.NODE_ENV === 'production')?'/':'http://localhost:4000'
const base = axios.create({baseURL})
const get = (url)=>base({method:'GET',url})
const post = (url,data)=>base({method:'POST',url,data})

function App() {
  let canvasRef = useRef()
  const [logIn,setLogIn] = useState(false)
  const token = useRef()

  const validationSchema = object().shape({
    username:string(),
    roomCode:string()
  })
  const processLogin = (t)=>{
    token.current = t
    setLogIn(true)
  }

  const submitData = async ({username,roomCode})=>{
    const {error,message,token} = (await post('/register',{username,roomCode})).data
    if(error) return cogoToast.error(message||"An error occur")
    else processLogin(token)
  }

  const createRoom = async ({username})=>{
    const {error,message,token} = (await post('/create',{username})).data
    if(error) return cogoToast.error(message || 'An error occur')
    else processLogin(token)
  }

  useEffect(()=>{
    if(token.current){
      Controller(canvasRef.current,token.current)
    }
  },[logIn])

  if(!logIn){
    return (
      <Router>
        <Switch>
          <Route path='/create'>
            <div className='container-fluid text-center'>
              <h1>Catan Online</h1>
              <p>Please enter your username</p>
              <Formik initialValues={{username:''}} onSubmit={createRoom}>
                {({handleChange,handleBlur,handleSubmit})=>(
                  <form className='form-group w-75 mx-auto' onSubmit={handleSubmit}>
                    <label className='font-weight-bold'>Username</label>
                    <input type='text' name='username' className='form-control' onChange={handleChange} onBlur={handleBlur}/>
                    <button className='mt-2 btn btn-success' type='submit'>Create a Room</button>
                  </form>
                )
                }
              </Formik>
              <Link to='/'>Join a room</Link>
            </div>
          </Route>
          <Route path='/' exact>
            <div className='container-fluid text-center'>
              <h1>Catan Online</h1>
              <p>Please enter your room code and username</p>
              <Formik initialValues={{roomCode:'',username:''}} onSubmit={submitData} validationSchema={validationSchema}>
                {({handleChange,handleBlur,handleSubmit})=>(
                  <form className='form-group w-75 mx-auto' onSubmit={handleSubmit}>
                    <label className='font-weight-bold'>Username</label>
                    <input type='username' name='username' className='form-control' onChange={handleChange} onBlur={handleBlur}/>
                    <label className='font-weight-bold'>Room Code</label>
                    <input type='text' name='roomCode' className='form-control' onChange={handleChange} onBlur={handleBlur}/>
                    <button className='mt-2 btn btn-success' type='submit'>Submit</button>
                  </form>
                )
                }
              </Formik>
              <Link to='/create'>Create a room</Link>
            </div>
          </Route>
        </Switch>
      </Router>
    )
  }
  else{
    return (
      <>
        <div className="d-flex align-items-center w-100 h-100 position-absolute">
          <canvas ref={canvasRef} style={{width:'850px',height:'650px'}} className='align-self-center mx-auto'></canvas>
        </div>
      </>
    )
  }
}

export default App;
